<template>
	<view class="">
			<popUp ref="subBind"></popUp>
			<view style="background-color: #2b95d0; position: fixed; bottom: 0; z-index: -2; height: 100vh; width: 750rpx;">
			</view>
			<view class="" style="height: 78rpx;"></view>
			<view class="df-jcc-aic">
				<image src="../../static/settings/TekygoLogos.png" mode="widthFix" style="width: 201rpx;"></image>
			</view>
			<view class="" style="height: 46rpx;"></view> 
			<view class="" style="position: relative;">
				<view class="f42rpx FFF ta-c fw800" style="margin-bottom: 55rpx;">
					 CONNECT DEVICE
				</view> 
				<view  class="" style="position: absolute; top: 8rpx; left: 78rpx;"
				hover-class="hover1" hover-start-time=0 hover-stay-time=150 >
					<image @click="goIndexx" src="../../static/settings/fanhuiS.png" mode="widthFix" style="width: 42rpx;"></image>
				</view> 
			</view>
			<view class="bgc_FFF" style="margin: 0 78rpx; border-radius: 51rpx; height: 688rpx;">
				<view class="" style="height: 88rpx;"></view>
				<view class="ta-c f33rpx color-H0f649c" style="margin: 0rpx 40rpx 27rpx;">
					Connect your hardware by scanning the QR code in your Portal.
				</view>
				<view class="f33rpx" style="color: #eb5120;">
					<view class="ta-c" style="line-height: 42rpx;">Please go to</view>
					<view class="ta-c" style="line-height: 42rpx;">Setting > General > Bind</view>
				</view>
				<view @click="saoYiSao" class="b-r-15 df-jcc-aic" 
				hover-class="hover" hover-start-time=0 hover-stay-time=150
				style="margin: 22rpx 54rpx 26rpx; height: 76rpx; background-color: #fecb4d;">
					<image  src="../../static/fristPage/syys.png" mode="" 
					style="width: 54rpx; height: 54rpx;"></image>
				</view>
				<view class="ta-c f33rpx color-H0f649c" style="margin: 0rpx 40rpx 37rpx;">
					If you don’t have a TekyGo portal please check
					<text @click="goHere" style="margin-left: 10rpx; border-bottom: 5rpx solid #0f649c; line-height: 25rpx;"
					>here</text>.
				</view>
				<view class="ta-c f33rpx color-H0f649c">
					Device not ready? 
				</view>
				<view class="ta-c f33rpx color-H0f649c fw800" @click="goIndexx" 
					hover-class="hover" hover-start-time=0 hover-stay-time=150
					style="text-decoration: underline; letter-spacing: -3rpx; white-space: nowrap"> 
					ADD LATER
				</view>

			</view>
	
	</view>
</template>

<script>
	export default{
		data(){
			return{
				id:''
			}
		},
		methods:{
			backRegisterss(){
				
			},
			async saoYiSao(){ 
				var that = this
				uni.scanCode({
					onlyFromCamera: true,                   
					success(res) {                                              
						that.id = res.result
						if(res.result.length == 32){
							uni.setStorageSync("id",that.id)
							console.log(that.id);
							that.scanCodes()
						}else{
							that.$refs.subBind.openPopup('Invalid code')
						}
 
					}
				
				})
			},
			//扫码绑定设备
			async scanCodes(){
				// var that = this
				const res = await this.$myRequest({
					url:'/api/device/bind'+'?processId='+ this.id,
				})
				console.log(res); 
				if(res.data.succeeded){
					console.log('绑定成功');
					this.$refs.subBind.openPopup('Binding success!')
					this.bindSyntony()
					uni.switchTab({
						url:'../index/index'
					})
				}else{
					console.log('绑定失败');
					this.$refs.subBind.openPopup(res.data.errmsg)
				}
			}, 
			
			async bindSyntony(){
				const res = await this.$myRequest({
					url:'/api/device/list' + '?page=1' +'&limit=1',
				})
				console.log('展示');
				console.log(res)
				console.log(res.data.data.records[0].serial);
				uni.setStorageSync("serial",res.data.data.records[0].serial)
				this.serial = res.data.data.records[0].serial
			},  
			
			goHere(){
				plus.runtime.openURL('https://www.tekygo.com');
			}, 
			goIndexx(){
				uni.setStorageSync('isLogin',true)
				uni.switchTab({
					url:'../index/index'
				})
			}
		}
	}
</script>

<style scoped>
	page{
		background-color: #2b95d0;
	} 
	.bgc-picture1{
		 z-index:-1; 
		 width:100%; 
		 height:100%; 
		 position: fixed;  
		 top:0; 
	}
</style>

